	module.exports = {"data":{"markdownRemark":{"html":"<p>自作PCに初トライ。あっさりうまくいき、それから暫く PC 自作にはまってずるずる引き込まれていた。 ちなみに名前の由来は中古で買ったPCケースのエンブレムにちなんだもの。 性能は<a href=\"/brezza5100cx\">先代</a>の 4 倍近く。結構もっさりしていた WindowsMe が割とサクサク動くようになったが、流石にこの環境で WindowsXP はかなり重かったようで大幅改造をすることに。  その後は、一部パーツを<a href=\"/Homebuilt-2\">Ver.2</a>に吸収され放置、それが<a href=\"/Homebuilt-3\">弐號機</a>に玉突き転属される際不足分をさらに吸収され余ったパーツは売却。確か1000円も行かなかった気がする。</p>\n<h2>当時はまったPCゲーム</h2>\n<p>DelightDelightReduplication</p>\n<h2>製作物・他やってたこと</h2>\n<ul>\n<li>\n<p>(BMSプレイヤー)Km-IIK</p>\n</li>\n<li>\n<p>(BMSプレイヤー)BMS ～ Bring your own Music &#x26; Sounds</p>\n<h2>性能</h2>\n</li>\n<li>\n<p>CPU:intel PentiumII(Klamath/266MHz)</p>\n</li>\n<li>\n<p>RAM:128MB</p>\n</li>\n<li>\n<p>@VGA:ATi XPART@PLAY98(3DRAGE-LTPRO:VRAM8MB)</p>\n</li>\n<li>\n<p>HDD:4GB+2GB(SCSI)</p>\n</li>\n<li>\n<p>CDD:50倍速</p>\n</li>\n<li>\n<p>FDD:3.5inch(3mode)×1</p>\n</li>\n<li>\n<p>NET:64kbps無線ISDN TA</p>\n</li>\n<li>\n<p>SOUND:YAMAHA YMF724</p>\n</li>\n<li>\n<p>OS:WindowsMe→WindowsXP</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2000-09-27T00:00:00+09:00","strDate":"2000/9/26","title":"(2000-2002) 世界最速1号Ver.1"}}},"pathContext":{"path":"/ja/blog/2000/09/27-homebuilt-1/","langKey":"ja"}}

